/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */
body{
 background-color: #fcf3e3;
 margin: 0;
 padding-bottom: 10em;
}

nav a {
  text-decoration: none;
  margin-left: 0.2em;
  margin-right: 0.2em;
  color: #555;
  font-size: 18px;

}

header.shrink, .blurheader.shrink {
  height: 8em;
}

header.shrink #headline {
  font-size: 2em;
  margin-top: 0.2em;
}

*, *:after, *:before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

header{
  background-color: white;
  z-index: 20;
  /* animation magic */
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  z-index: 20;
}

header, .blurheader {
    background: rgba(255, 255, 255, .6);
    position: fixed;
    height: 12em;
    width: 100%;
    left:0;
    right:0;
    top:0;
}

.blurheader {
    overflow: hidden;
    background: #ABF1FD;
    left: 0;
    z-index: 19;
}

header #headline {
   text-align: center;
   font-size: 3em;
   font-family: 'Kaushan Script', cursive;
   margin-bottom: auto;
   -webkit-transition: all 0.1s ease-in-out;
   -moz-transition: all 0.2s ease-in-out;
   transition: all 0.2s ease-in-out;
 }


 header hr.underheadline {
   width: 25em;
   margin-top: -0.6em;
   color: black;
   /* animation magic */
   -webkit-transition: all 0.1s ease-in-out;
   -moz-transition: all 0.2s ease-in-out;
   transition: all 0.2s ease-in-out;
 }

.schedule {
  display: inline-block;
  margin-bottom: 1px;
  margin-top: 1px;
  font-weight: normal;
}

span.timestamp {
  font-style: italic;
  padding-left: 0.4em;
}

 header.shrink hr.underheadline{
   width: 16em;
   margin-top: -0.6em;
 }

 a.landmark:before {
   display: block;
   content: " ";
   margin-top: -10em;
   height: 10em;
   visibility: hidden;
 }

@media (max-width: 380px) {
  header, .blurheader {
    max-height: 9em;
  }

  header.shrink, .blurheader.shrink {
    max-height: 7em;
  }

  header #headline {
    font-size: 2em;
  }

  header.shrink #headline {
    font-size: 1.5em;
  }

  header #date {
    font-size: 1em;
  }

  header.shrink #date {
    font-size: 0.9em;
  }


  header nav a {
    font-size: 0.8em;
  }
  header hr.underheadline {
    width: 16em;
  }

  header.shrink hr.underheadline {
    width: 12em;
  }

  a.landmark:before {
    display: block;
    content: " ";
    margin-top: -7em;
    height: 7em;
    visibility: hidden;
  }
}

p.subtext {
  font-size: 0.7em;
}

article {
